import React from "react";
import { MyContext } from "./MyContext";
import {
  hero_slider_context,
  offers_content,
  Story_context,
  Special_context,
  Menu_context,
  Reservation_context,
  Event_context
} from "./context";

const ContextProvider = ({ children }) => {
  return (
    <MyContext.Provider
      value={{
        hero_slider_context,
        offers_content,
        Story_context,
        Special_context,
        Menu_context,
        Reservation_context,
        Event_context
      }}
    >
      {children}
    </MyContext.Provider>
  );
};

export default ContextProvider;
