import React, { useContext } from 'react';
import './Menus.css'
import { MyContext } from '../../Context/MyContext';
import Menu from '../../Container/Menu/Menu';
import ButtonType1 from '../../Elements/ButtonType1/ButtonType1';
const Menus = () => {
  const {Menu_context} = useContext(MyContext);
  return (
    <div id="Menus" className="menu_main_container">
      <div className="menu_main_background_1"></div>
      <div className="menu_main_background_2"></div>
      <div className="menu_main_heading_container">
        <div className="menu_main_heading">
          <p className="font-type_1">{Menu_context.menu_small_heading}</p>
          <div className="royalty">
            <div className="royaltySign">
              <div className="anims_block"></div>
            </div>
          </div>
          <h1 className="heading_type-2 cl_w">{Menu_context.menu_heading}</h1>
        </div>
      </div>

      <div className="menu_main_inner_container">
        {
          Menu_context.menus.map((content,index)=>{
            return <Menu key={index} content={content}></Menu>
          })
        }
      </div>

      <div className="menu_button_container">
        <p className="p_type_medium cl_w">
          {Menu_context.menu_bottom_content.content} <span className="cl_g">{Menu_context.menu_bottom_content.from}</span> to 
          <span className="cl_g"> {Menu_context.menu_bottom_content.to}</span>
        </p>
        <div className="menu_button">
          <ButtonType1 button_text={Menu_context.menu_button_content.split("")} index={'view_menu_button'} link={"#Menus"} classes={''}></ButtonType1>
        </div>
      </div>
    </div>
  );
};

export default Menus;
