import React, { useContext, useEffect } from "react";
import gsap from "gsap";
import "./Offers.css";
import { MyContext } from "../../Context/MyContext";
import OffersMenu from "../../Elements/OfferMenu/OffersMenu";
const Offers = () => {
  const { offers_content } = useContext(MyContext);

  useEffect(() => {
    gsap.to([".service_shape_1",".service_shape_2"], {
      yPercent: 10,
      xPercent: 2,
      yoyo: !0,
      duration: 5,
      repeat: -1,
      ease: "power1.inOut",
    });
  },[]);
  return (
    <div id="Offers" className="offers_main_container w_100">
      <div className="offers_inner_container">
        <div className="offers_content_top">
          <div className="offers_heading">
            <div className="font-type_1">
              <p>{offers_content.heading}</p>
            </div>
            <div className="royalty">
              <div className="royaltySign">
                <div className="anims_block"></div>
              </div>
            </div>
          </div>
          <div className="offers_desc font-type_2">
            <p>{offers_content.desc}</p>
          </div>
        </div>
        <div className="offers_bottom_main_container">
          {offers_content.offers.map((content, index) => {
            return <OffersMenu content={content} index={index}></OffersMenu>;
          })}
        </div>
      </div>
      <div className="service_shape_1"></div>
      <div className="service_shape_2"></div>
    </div>
  );
};

export default Offers;
