import React, { useContext, useEffect, useRef, useState } from "react";
import "./Hero.css";
import { MyContext } from "../../Context/MyContext";
import HeroSlider from "../../Elements/Heroslider/Heroslider";
const Hero = () => {
  const [hero_state, setHeroState] = useState(0);
  const { hero_slider_context } = useContext(MyContext);
  const hero_slider_size = useRef(hero_slider_context.length);

  useEffect(() => {
    setTimeout(() => {
      setHeroState((prev) => ((prev + 1) % hero_slider_size.current));
    }, 8000);
  }, [hero_state]);

  return (
    <div className="hero_container w_100">
      <div className="hero_slider_container w_100 h_100">
        {hero_slider_context.map((content, index) => {
          return (
            <HeroSlider
              content={content}
              index={index}
              isActive={index === hero_state}
              key={index}
            />
          );
        })}
      </div>
    </div>
  );
};

export default Hero;
