import React, { useEffect, useState } from 'react'
import Home from './Component/Home/Home'
import Footer from './Component/Footer/Footer'
import Menus from './Component/Menus/Menus'
import Reservation from './Container/Reservation/Reservation'
import Strength from './Container/Strength/Strength'
import Events from './Container/Events/Events'
import './App.css'

const App = () => {
  const [slideBarState,setSlideBarState] = useState(false);
  const [previousPosition,setPreviousPosition] = useState(0);
  const [currentNavbarState,setCurrentNavbarState] = useState('transactive');

  function setNavbarState(){
    const current = window.scrollY ; 
    current<=50?setCurrentNavbarState('transactive'):current<previousPosition?setCurrentNavbarState('up'):setCurrentNavbarState('down');
    setPreviousPosition(current);
  }

  useEffect(()=>{
    
    window.addEventListener('scroll',setNavbarState);

    return ()=>{
      window.removeEventListener('scroll',setNavbarState);
    }
  },[previousPosition])
//${slideBarState?'':'active'}
  return (
    <div id="#" className={`app_main_container active`}>
      <Home currentNavbarState={currentNavbarState} slideBarState={slideBarState} setSlideBarState={setSlideBarState} />
      <Menus />
      <Reservation />
      <Strength />
      <Events />
      <Footer />
    </div>
  )
}

export default App
