import React, { useContext, useEffect, useRef } from "react";
import gsap from "gsap";
import "./Story.css";
import { MyContext } from "../../Context/MyContext";
import ButtonType1 from "../../Elements/ButtonType1/ButtonType1";
const Story = () => {
  const { Story_context } = useContext(MyContext);
  const story_container_ref = useRef(null);
  useEffect(() => {
    const handleMouseMoveOverStory = (event) => {
      const { clientX, clientY } = event;
      const innerHeight = window.innerHeight;
      const innerWidth = window.innerWidth;
      const d = 1 - clientX / (innerWidth / 2);
      const m = 1 - clientY / (innerHeight / 2);
      gsap.to(".story_image_2", {
        x: d * 20,
        y: m * 20,
        duration: 0.001,
        ease: "power1.in",
      });
      gsap.to(".story_image_1", {
        x: d * 10,
        y: m * 10,
        duration: 0.001,
        ease: "power1.in",
      });
    };

    story_container_ref.current.addEventListener(
      "mousemove",
      handleMouseMoveOverStory
    );

    return () => {
      story_container_ref.current.removeEventListener(
        "mousemove",
        handleMouseMoveOverStory
      );
    };
  }, []);

  return (
    <div id="Story" className="story_main_container" ref={story_container_ref}>
      <div className="story_inner_container">
        <div className="story_content_left">
          <div>
            <h2 className="font-type_1">{Story_context.story_heading_small}</h2>
          </div>
          <div className="royalty">
            <div className="royaltySign">
              <div className="anims_block"></div>
            </div>
          </div>
          <div className="story_heading">
            <h1 className="heading_type-2">{Story_context.story_heading}</h1>
          </div>
          <div className="story_desc">
            <p className="font-type_2">{Story_context.story_para_1}</p>
          </div>
          <div className="story_desc">
            <p className="font-type_2">{Story_context.story_para_2}</p>
          </div>
          <div className="story_booking">
            <h4 className="font-type_2">{Story_context.booking}</h4>
            <div className="mobile_number">
              <p>{Story_context.mobile_number}</p>
            </div>
          </div>
          <div>
            <ButtonType1
              button_text={Story_context.button_content.split("")}
              index={"story_content"}
              link={"#About_us"}
              classes={""}
            ></ButtonType1>
          </div>
        </div>
        <div className="story_content_right">
          <div className="story_content_right_inner_container">
            <div className="story_image_1">
              <div className="back_pattern"></div>
              <img src={Story_context.image_1} alt="story_image_1" />
            </div>
            <div className="story_image_2">
              <div className="back_pattern"></div>
              <img src={Story_context.image_2} alt="story_image_2" />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Story;
