import React, { useEffect, useRef } from "react";
import "./ButtonType1.css";
import gsap from "gsap";

const ButtonType1 = ({ button_text, index, link}) => {
  const buttonAnimationRef = useRef(null);

  const handleMouseEnter = () => {
    buttonAnimationRef.current.play();
  };

  const handleMouseLeave = () => {
    buttonAnimationRef.current.reverse();
  };

  useEffect(() => {
    buttonAnimationRef.current = gsap
      .timeline({ paused: true, reversed: true })
      .to(`#button_type_1-${index} .button_type_1_anim`, {
        duration: 0.5,
        stagger: {
          amount: 0.5,
          grid: [2, button_text.length],
          from: "start",
          axis: "x",
        },
        yPercent: -100,
        ease: "Power1.out",
      });
  }, [button_text.length, index]);

  return (
    <div
      id={`button_type_1-${index}`}
      className="button_type_1 tal_ce"
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      <div className="button_type_1_inner">
        <a href={link} className="button_type_1_text_top">
          {button_text.map((content, idx) => (
            <span key={idx} className={`button_type_1_anim anim-${idx}`}>
              {content === " " ? "\u00A0" : content}
            </span>
          ))}
        </a>
        <a href={link} className="button_type_1_text_bottom tal_ce">
          {button_text.map((content, idx) => (
            <span key={idx} className={`button_type_1_anim anim-${idx}`}>
              {content === " " ? "\u00A0" : content}
            </span>
          ))}
        </a>
      </div>
    </div>
  );
};

export default ButtonType1;
